<script lang="ts">
	import { onMount } from 'svelte';
	import { 
		initializePlatformDetection, 
		getIsAndroidTV, 
		shouldUseTenFootUI,
		getFontSizes,
		getSpacing
	} from '$lib/utils/tv-detection';

	interface Props {
		children?: any;
	}

	let { children }: Props = $props();

	let isAndroidTV = $state(false);
	let useTenFootUI = $state(false);
	let fontSizes = $state(getFontSizes());
	let spacing = $state(getSpacing());

	onMount(() => {
		initializePlatformDetection();
		isAndroidTV = getIsAndroidTV();
		useTenFootUI = shouldUseTenFootUI();
		fontSizes = getFontSizes();
		spacing = getSpacing();
	});

	// Dynamic CSS classes based on platform
	const containerClasses = $derived(() => {
		const base = 'min-h-screen bg-black text-white';
		
		if (useTenFootUI) {
			return `${base} tv-layout overflow-hidden`;
		}
		
		return `${base} web-layout`;
	});

	const contentClasses = $derived(() => {
		if (useTenFootUI) {
			return 'tv-content px-16 py-8'; // Extra padding for TV safe areas
		}
		
		return 'web-content px-4 py-2 sm:px-6 sm:py-4';
	});
</script>

<div class={containerClasses} data-platform={isAndroidTV ? 'tv' : 'web'}>
	<div class={contentClasses}>
		{@render children?.()}
	</div>
</div>

{#if useTenFootUI}
	<!-- TV-specific focus indicator styles -->
	<style>
		:global(.tv-layout) {
			/* Ensure content fits in TV safe areas */
			padding: calc(5vh) calc(5vw);
		}

		:global(.tv-layout *:focus) {
			outline: 4px solid #dc2626 !important;
			outline-offset: 4px !important;
			border-radius: 8px;
		}

		:global(.tv-layout .movie-card:focus) {
			transform: scale(1.15) !important;
			z-index: 100 !important;
			box-shadow: 0 25px 50px -12px rgba(220, 38, 38, 0.5) !important;
		}

		:global(.tv-layout .btn) {
			min-height: 60px;
			min-width: 120px;
			font-size: 1.25rem;
			font-weight: 600;
		}

		:global(.tv-layout .btn:focus) {
			transform: scale(1.1);
			background-color: #dc2626 !important;
			color: white !important;
		}

		:global(.tv-layout h1) {
			font-size: 4rem !important;
			line-height: 1.1 !important;
		}

		:global(.tv-layout h2) {
			font-size: 3rem !important;
			line-height: 1.2 !important;
		}

		:global(.tv-layout h3) {
			font-size: 2rem !important;
			line-height: 1.3 !important;
		}

		:global(.tv-layout p) {
			font-size: 1.5rem !important;
			line-height: 1.4 !important;
		}

		/* Larger interactive elements for D-pad navigation */
		:global(.tv-layout button) {
			min-height: 60px;
			padding: 1rem 2rem;
			font-size: 1.25rem;
			border-radius: 12px;
		}

		:global(.tv-layout input) {
			min-height: 60px;
			padding: 1rem;
			font-size: 1.25rem;
			border-radius: 8px;
		}

		/* Movie grid adjustments for TV */
		:global(.tv-layout .movie-grid) {
			gap: 2rem;
		}

		:global(.tv-layout .movie-row) {
			margin-bottom: 3rem;
		}

		/* Enhanced focus transitions */
		:global(.tv-layout *) {
			transition: all 0.2s ease-out;
		}
	</style>
{/if}

<style>
	.web-layout {
		/* Default web styles */
	}

	.tv-content {
		/* TV-specific content styling */
		box-sizing: border-box;
	}

	.web-content {
		/* Web-specific content styling */
	}
</style>