<script lang="ts">
	import { onMount } from 'svelte';
	import '../app.css';
	import Header from '$lib/components/Header.svelte';
	import KeyboardNavigation from '$lib/components/KeyboardNavigation.svelte';
	import TVLayout from '$lib/components/TVLayout.svelte';
	import { initializePlatformDetection, getIsAndroidTV } from '$lib/utils/tv-detection';
	import { initializeTVNavigation } from '$lib/utils/tv-navigation';

	let { children } = $props();
	let isAndroidTV = $state(false);

	onMount(() => {
		// Initialize platform detection first
		initializePlatformDetection();
		isAndroidTV = getIsAndroidTV();
		
		// Initialize TV navigation if on Android TV
		if (isAndroidTV) {
			initializeTVNavigation();
		}
		
		// Initialize keyboard navigation
		import('$lib/stores/keyboard');
	});
</script>

<TVLayout>
	{#snippet children()}
		{#if !isAndroidTV}
			<Header />
		{/if}
		<KeyboardNavigation />
		<main>
			{@render children()}
		</main>
	{/snippet}
</TVLayout>
